 class ConnectionQueue{

    constructor(){
        this.queue = new Map();
    }

    addWSConnection(userId, ws){
        this.queue.set(userId, ws);
    }

    getWSConnection (userId){
        if (this.queue.has(userId))
            return this.queue.get(userId);

        return null;
    }

    closeWSConnection(userId){
        this.queue.delete(userId);
    }
}
module.exports = ConnectionQueue;